import ReactApexChart from "react-apexcharts";

const Bar = ({ seriesData, categories }) => {
    const dataProcesada = seriesData[0].datasets[0].data;
    //console.log(seriesData[0].datasets[0].data);
  const data = {
    series: [
      {
        data: dataProcesada,
      },
    ],
    options: {
      chart: {
        type: "bar",
        height: 350,
      },
      plotOptions: {
        bar: {
          borderRadius: 4,
          horizontal: true,
        },
      },
      dataLabels: {
        enabled: false,
      },
      xaxis: {
        categories: categories,
      },
    },
  };
  return (
    <div>
      <ReactApexChart
        options={data.options}
        series={data.series}
        type="bar"
        height={210}
      />
    </div>
  );
};

Bar.defaultProps = {
  seriesData: [],
  categories: [],
};

export default Bar;
